import React from 'react';
import ListenerMixin from 'alt/mixins/ListenerMixin';
import AppStore from 'store/appStore';

export default React.createClass({
  displayName: 'NavBar',
  mixins: [ListenerMixin],
  contextTypes: {
    router: React.PropTypes.func
  },

  propTypes: {
    items: React.PropTypes.object.isRequired
  },

  getInitialState() {
    return AppStore.getState();
  },

  componentDidMount() {
    AppStore.listen(this.onChange);
  },

  // onChange(state) {
  //   console.log(state);
  // },

  showPrevious(id) {
    id = parseInt(id) - 1;

    if (this.props.items[id] !== undefined) {
      return this.context.router.transitionTo('item', {id});
    }
    else {
      console.log('you are at the end of the list');
    }
  },

  showNext(id) {
    id = parseInt(id) + 1;

    if (this.props.items[id] !== undefined) {
      return this.context.router.transitionTo('item', {id});
    }
    else {
      console.log('you are at the end of the list');
    }
  },

  render() {
    let {id} = this.context.router.getCurrentParams();

    return (
      <div className="navbar">
        <ul>
        <li className="btn glyphicon glyphicon-arrow-left" onClick={this.showPrevious.bind(this, id)}>Previous</li>
        <li className="btn glyphicon glyphicon-arrow-right" onClick={this.showNext.bind(this, id)}>Next</li>
        </ul>
      </div>
    );
  }
});



